import React from 'react';

import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import { useFormik } from 'formik';
import { Navigate, NavLink } from 'react-router-dom';

import { useAppDispatch, useAppSelector } from '../../common/hooks/hooks';
import { CustomButton } from '../../components/customButton/CustomButton';
import { Input } from '../../components/input/Input';

import { sendLoginData } from './loginReducer';

type FormikErrorType = {
  email?: string;
  password?: string;
  rememberMe?: boolean;
};

export const Login = () => {
  const dispatch = useAppDispatch();
  const status = useAppSelector(state => state.app.status);
  const isLoggedIn = useAppSelector(state => state.login.isLoggedIn);

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
      rememberMe: false,
    },
    validate: values => {
      const errors: FormikErrorType = {};

      if (!values.email) {
        errors.email = '   ';
      } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'Email  ';
      }
      if (!values.password) {
        errors.password = '   ';
      }

      return errors;
    },
    onSubmit: values => {
      const data = {
        email: values.email,
        password: values.password,
        rememberMe: values.rememberMe,
      };

      dispatch(sendLoginData(data));
      formik.resetForm();
    },
  });

  if (isLoggedIn) return <Navigate to="/packs?accessory=All" />;

  return (
    <div className="frame">
      <div className="title">Sign in</div>
      <form className="form" onSubmit={formik.handleSubmit}>
        <Input
          placeholder="Email"
          {...formik.getFieldProps('email')}
          error={formik.errors.email && formik.touched.email}
          errorText={formik.errors.email}
        />
        <Input
          placeholder="Password"
          password
          {...formik.getFieldProps('password')}
          error={formik.errors.password && formik.touched.password}
          errorText={formik.errors.password}
        />
        <FormControlLabel
          style={{ marginLeft: 0 }}
          label="Remember me"
          labelPlacement="start"
          control={
            <Checkbox
              checked={formik.values.rememberMe}
              {...formik.getFieldProps('rememberMe')}
            />
          }
        />
        {/* <NavLink className="login__forgotLink" to="/password-recovery"> */}
        {/*  Forgot Password */}
        {/* </NavLink> */}
        <div className="submit">
          <CustomButton title="Sign in" disabled={status === 'loading'} submit />
        </div>
      </form>
      <div className="login__text">Dont have an account?</div>
      <NavLink className="login__link" to="/registration">
        Sign Up
      </NavLink>
    </div>
  );
};
